.resume-section {
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }
  
  .section-title {
    font-size: 24px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
  }
  
  .section-title i {
    margin-right: 10px;
    color: #5d8aa8; /* Cor do ícone */
  }
  
  .education-item {
    background-color: #f9f9f9; /* Cor de fundo padrão */
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
  }
  
  .education-item:hover {
    background-color: #f0f0f0; /* Cor de fundo ao passar o mouse */
  }
  
  .education-item .year {
    font-size: 14px;
    color: #888;
    display: block;
    margin-bottom: 5px;
  }
  
  .education-item h3 {
    font-size: 18px;
    margin: 5px 0;
  }
  
  .education-item p {
    font-size: 14px;
    color: #666;
    margin: 0;
  }
  
  .skills-section .skill {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .skill-name {
    font-size: 16px;
    color: #333;
    width: 20%;
  }
  
  .skill-level {
    font-size: 14px;
    color: #666;
  }
  
  .progress-bar {
    width: 70%;
    height: 8px;
    background-color: #e0e0e0;
    border-radius: 5px;
    overflow: hidden;
  }
  
  .progress {
    height: 100%;
    border-radius: 5px;
    transition: width 0.3s ease-in-out;
  }
  