.profile-card {
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 100%;
  max-width: 350px; /* Ajuste conforme necessário */
  margin-bottom: 20px;
}

.profile-image {
  width: 200px; /* Aumentando o tamanho da imagem */
  height: 200px;
  border-radius: 50%;
  margin-bottom: 20px;
  object-fit: cover;
}

.title {
  color: #777;
  margin-bottom: 20px;
}

.social-icon {
  background-color: #f5f7fa;
  border-radius: 50%;
  padding: 10px;
  margin: 0 5px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  color: #555;
  transition: background-color 0.3s, color 0.3s;
}

.social-icon:hover {
  background-color: #5d8aa8;
  color: white;
}

.contact-info {
  margin-top: 20px;
}

.contact-item {
  padding: 8px;
  margin: 2px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-item i {
  margin-right: 10px; /* Espaçamento entre o ícone e o texto */
  font-size: 18px; /* Ajuste o tamanho do ícone, se necessário */
}

.download-cv {
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  color: #fff;
  background-color: #5d8aa8;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s ease-in-out;
  margin-top: 20px;
}

.download-cv:hover {
  background-color: #5d8aa8;
}
