.works-section {
  padding: 20px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.works-section h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

.works-section p {
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 20px;
}

.github-link {
  display: inline-flex;
  align-items: center;
  padding: 10px 20px;
  font-size: 16px;
  color: #fff;
  background-color: #333;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s ease-in-out;
}

.github-link:hover {
  background-color: #5d8aa8;
}

.github-icon {
  margin-right: 10px;
  font-size: 24px;
}
