.contact-section {
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }
  
  .contact-section h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .contact-section p {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  .contact-links {
    display: flex;
    gap: 30px; /* Espaço entre os ícones */
  }
  
  .contact-link {
    text-decoration: none;
  }
  
  .contact-icon-large {
    font-size: 48px; /* Tamanho grande para o ícone */
    color: #333;
    transition: color 0.3s ease-in-out;
  }
  
  .contact-link:hover .contact-icon-large {
    color: #5d8aa8; /* Muda a cor ao passar o mouse */
  }
  
  .whatsapp-link .contact-icon-large {
    color: #25d366; /* Cor verde para o ícone do WhatsApp */
  }
  
  .whatsapp-link:hover .contact-icon-large {
    color: #128c7e; /* Tom mais escuro de verde ao passar o mouse */
  }
  