html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background-color: #f5f7fa; /* Altere para a cor desejada */
}

.App {
  font-family: 'Arial', sans-serif;
  background-color: inherit; /* Herdar o fundo do corpo */
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: filter 0.3s ease-in-out; /* Transição suave */
}

.App.negative {
  filter: invert(1) hue-rotate(180deg); /* Aplica o filtro negativo */
}


.App.negative .no-invert {
  filter: invert(1) hue-rotate(180deg); /* Cancela o efeito de inversão para manter as cores originais */
}

.toggle-negative-mode {
  position: fixed;
  top: 10px;
  left: 10px;
  width: 50px;
  height: 50px;
  font-size: 24px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.toggle-negative-mode:hover {
  background-color: #708090;
}
.container {
  display: flex;
  width: 80%;
  max-width: 1200px;
  margin: 20px auto;
  padding: 20px;
}

.navigation-container {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
}

.left-section {
  flex: 2;
  padding-right: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.right-section {
  flex: 1;
  background-color: transparent;
  margin-left: 20px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.navigation-menu {
  display: flex;
  justify-content: center;
  gap: 20px; /* Espaço entre os botões */
  box-sizing: border-box;
  background-color: #ffffff;
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 30px;
  margin-bottom: 30px;
  margin-left: 22px;
}

.navigation-menu button {
  background-color: #d4d4d4;
  border: 1px solid #bebebe;
  padding: 10px;
  font-size: 14px;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;
  border-radius: 15px;
  box-sizing: border-box;
  box-shadow: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
}

.navigation-menu button:hover {
  color: #5d8aa8;
  border-color: #5d8aa8;
}

.navigation-menu button.active {
  color: #5d8aa8;
  border-color: #5d8aa8;
}

.content {
  background-color: transparent;
  padding: 20px;
  border-radius: 10px;
  margin-top: 0;
  width: 100%;
  box-sizing: border-box;
  box-shadow: none;
}
